/*-------- Dropdown --------*/

.dropdown {
    padding-top: 10px;
    position: absolute;
    top: 100%;
    left: 0;

    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-out;
    
    &__list {
        width: max-content;
        padding: 15px;

        background-color: $blue-dark;
        border-radius: 5px;
    }

    &__item:not(:first-child) {
        margin-top: 10px;
        padding-top: 10px;
        border-top: 1px dotted #e6e6e63d;
    }

    &__link {
        display: flex;
        align-items: center;

        position: relative;

        font-family: $font-m;
        color: #ffffff;
        transition: padding-left 0.5s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &__link:before {
        content: "";
        width: 15px;
        height: 2px;
        background-color: #ffffff;

        position: absolute;
        left: 0;

        transform: translateX(-50%) scaleX(0);
        transition: transform 0.5s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &__link--submenu:before {
        width: 10px;
        background-color: #2a9df4;
    }

    &__item:hover > &__link:before {
        transform: translateX(0%) scaleX(1);
    }

    &__item:hover > &__link {
        padding-left: 20px;
    }

    &__submenu {
        max-height: 0;
        min-height: 0;
        overflow: hidden;
        transition: min-height 0.5s cubic-bezier(0.77, 0, 0.175, 1); 
    }

    &__submenu-list {   
        padding-left: 15px;   
        margin-top: 10px; 
        padding-top: 10px;
        border-top: 1px dotted #e6e6e63d;
    }
}

.dropdown {

    &--header {
        transform: translateY(20%);
        transition: opacity 0.3s ease-out, transform 0.4s ease-out;
    }

    &--header.hidden {
        visibility: hidden;
    }

    &--header.animate-out {
        visibility: visible;
    }
}

@keyframes fade-out {
    
    0% {
        opacity: 1;
        visibility: visible;
    }

    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@media (max-width: 989px) {

    .dropdown {

        &--header {
            width: max-content;
            max-height: 0;
            min-height: 0;
            padding-top: 0;
            position: static;
            opacity: 1;
            visibility: visible;
            transform: unset;   
            transition: all 0.2s ease-out;
        }

        &--header &__list {
            background-color: transparent;
        }

        &--header &__link {
            display: inline-flex;
        }

        &--header &__submenu {
            width: 100%;
        }
    }
}